<template>
  <div class="top-box">
    <div class="bg-box">
      <div class="shadow"></div>
      <img class="bg-img" v-if="result" :src="result.playlist.coverImgUrl" />
    </div>
    <div class="desc-box">
      <div class="left">
        <img class="circle" v-if="result" :src="result.playlist.coverImgUrl" />
      </div>
      <div class="right">
        <span class="desc-title">歌单简介：</span>
        <span class="desc" v-if="result">{{ result.playlist.description }}</span>
      </div>
    </div>
    <div class="title">歌曲列表
          <!-- <span class="tg"></span> -->
          <span class="list-icon"></span>
    </div>
        <loading v-if="!result"></loading>

    <song-list
      @play="$emit('play', $event)"
      :songlist="songList"
      :playMusicID="playMusicID"
    ></song-list>
    <div class="combox">
      <div class="com-list">
        <div class="title">最新评论
          <!-- <span class="tg"></span> -->
          <span class="com-icon"></span>
        </div>
        <div class="user" v-for="(user,index) in comment" :key="index">
          
            <div class="u-head">
              <img :src="user.user.avatarUrl" alt="">
            </div>
          <div class="right">
            <div class="u-name">{{user.user.nickname}}
                <span class="u-vip" v-if="user.user.vipRights!=null"></span>
            </div>
            <div class="u-time">{{timemake(user.time)}}</div>
            <div class="u-talk">{{user.content}}</div>
            <div class="u-icon"></div>
          </div>
        </div>
      </div>
    </div>
    <div class="foot">
      <div class="thing">
        <img src="@/assets/images/logoZ-blue.png" alt="" />
        <div class="right">
          <div class="foot-logo"></div>
          <div class="foot-english"></div>
        </div>
      </div>
      
      <div class="open-download">
        <a href="#"><span>用心灵创造极致，用音乐歌唱唯一</span></a>
      </div>
    </div>    
  </div>
</template>

<script>
import SongList from "../components/SongList.vue";
export default {
  props: {
    listid: [String, Number],
    playMusicID: Number,
  },
  components: {
    SongList,
  },
  data() {
    return {
      songList: [],
      result: null,
      comment: [],
    };
  },
  methods:{
        timemake(value) {
        var date = new Date(value);
        var Y = date.getFullYear() + '年';
        var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '月';
        var D = date.getDate() + '日';
        return Y+M+D
        }
  }
  ,computed:{},
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      vm.axios.get(`/playlist/detail?id=${vm.listid}`).then((result) => {
        vm.result = result;
        let re = result.playlist.tracks.map((v, index) => {
          v.song = {
            privilege: result.privileges[index],
            artists: v.ar,
            album: v.al,
          };
          return v;
        });
        vm.songList = re;
      });
      vm.axios.get(`/comment/playlist?id=${vm.listid}`).then((res) => {

        vm.comment = res.comments
      });
    });
  },
};
</script>

